@import "./partials/normalize";
@import "./partials/variables";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');

* {
    padding: 0;
    margin: 0;
    list-style-type: none;
    box-sizing: border-box;
    outline: none;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    position: relative;
    background: #000;
}

.container {
    margin: 0 auto;
    max-width: 1470px;
    position: relative;
    padding: 0 15px; 
    height: 100%; }

img {
    max-width: 100%;
    object-fit: cover; 
}
a {
    text-decoration: none;
    cursor: pointer;
    transition: $transition;
}

/* ========================================== Header ========================================== */
.header {
    margin-bottom: 185px;
    padding-top: 30px;
    &.active {
        position: fixed;
        width: 100%;
        z-index: 10;
        background: #000;
    }
    &-row {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}
.contact-btn {
    background: $blue;
    height: 60px;
    max-width: 172px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 90px;
    font: 21px $bold;
    color: #fff;
    transition: $transition;
    &:hover {
        background: #0066CC;
    }
}
.logo {
    font: 17px $regular;
    color: #fff;
}
.menu {
    display: flex;
    align-items: center;
    &-item {
        margin-right: 90px;
        display: inline-flex;
        align-items: center;
        &:last-of-type {
            margin-right: 0;
        }
        &.active {
            .sub-menu {
                opacity: 1;
                visibility: visible;
            }
            .menu-trigger {
                transform: rotate(180deg);
            }
        }
    }
    &-link {
        font: 21px $regular;
        color: #fff;
        text-transform: uppercase;
    }
    &-trigger {
        outline: none;
        cursor: pointer;
        background: transparent;
        border: none;
        transition: $transition;
        margin-left: 10px;
        display: inline-flex;
        align-items: center;
    }
}
.sub-menu {
    position: absolute;
    display: flex;
    background: #171717;
    padding: 70px 90px 70px 75px;
    max-width: 1100px;
    width: 100%;
    z-index: 2;
    justify-content: space-between;
    left: 50%;
    transform: translateX(-50%);
    transition: $transition;
    opacity: 0;
    visibility: hidden;
    top: 70px;
}
.sub-column:last-of-type {
    .sub-list {
        column-count: 2;
        column-gap: 100px;
    }
}
.sub-name {
    margin-bottom: 30px;
    color: #0077ED;
    font: 21px $bold;
}
.sub-list {
    &__link {
        color: #A8A8A8;
        text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.33);
        font: 21px $regular;
        transition: $transition;
        &:hover {
            color: $blue;
        }
    }
    &__item {
        margin-bottom: 40px;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}
.mobile-menu__item {
    margin-bottom: 20px;
}
.mobile-menu__link {
    color: #FFFFFF;
    font: 17px $semibold;
    text-transform: uppercase;
    transition: $transition;
}
.mobile-top {
    position: relative;
    width: 100%;
    padding-bottom: 20px;
    border-bottom: 1px solid#0066CC;
    display: flex;
    align-items: center;
    &.active {
        .mobile-menu__link {
            padding-left: 40px;
        }
        .menu-control {
            right: initial;
            left: 10px;
            svg {
                transform: rotate(180deg);
                width: 10px;
                height: 15px;
            }
            path {
                fill: #0077ED;
            }
        }
    }
}
.cat-menu {
    padding: 25px 20px;
    display: none;
    &__link {
        color: #FFFFFF;
        font: 17px $semibold;
        text-transform: uppercase;
        transition: $transition;
    }
}
.cat-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 30px;
    margin-bottom: 20px;
    border-bottom: 1px solid#0066CC;
    width: 100%;
    transition: $transition;
    .menu-control {
        position: static;
    }
    &.active {
        border-color: transparent;
        margin-bottom: 0;
        .menu-control {
            transform: rotate(90deg);
        }
    }
}
.menu-control {
    position: absolute;
    right: 0;
    outline: none;
    border: none;
    background: transparent;
    transition: $transition;
    display: inline-flex;
    align-items: center;
    justify-content: flex-end;
    width: 15px;
}
.inner-list {
    display: none
}
.inner-list__item {
    display: flex;
    align-items: center;
    margin-bottom: 45px;
}
.inner-list__link {
    text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.33);
    color: #A8A8A8;
    font: 17px/21px $regular;
    position: relative;
    display: inline-flex;
    align-items: center;
    &::before {
        content: '';
        width: 5px;
        height: 5px;
        background: linear-gradient(112.29deg, #00E0FF 19.07%, #00A3FF 81.84%);
        border-radius: 555px;
        display: inline-block;
        margin-right: 12px;
    }
}
.mobile-wrapper {
    position: fixed;
    left: 0;
    top: 0px;
    width: 100%;
    height: 100%;
    background: #000;
    z-index: 10;
    display: none;
    .mobile-layout {
        overflow: auto;
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        height: 100%;
        padding-top: 100px;
    }
}
/* ========================================== Header ========================================== */

/* ========================================== We ========================================== */
.we {
    margin-bottom: 186px;
    &-title {
        text-align: center;
        margin: 0 auto 40px;
        max-width: 690px;
        width: 100%;
        font: 48px/58px $bold;
        color: #fff;
        text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
        span {
            color: $blue;
        }
    }
    &-intro {
        text-align: center;
        margin-bottom: 40px;
        text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.33);
        color: #A8A8A8;
        font: 21px $semibold;
    }
    &-btn {
        background: $blue;
        height: 64px;
        max-width: 193px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 90px;
        font: 21px $bold;
        margin: 0 auto;
        color: #fff;
        transition: $transition;
        &:hover {
            background: #0066CC;
        }
    }
}
/* ========================================== We ========================================== */

/* ========================================== About ========================================== */
.about {
    margin-bottom: 170px;
    &-row {
        display: flex;
        justify-content: space-between;
    }
    &-title {
        font: 48px $bold;
        margin-bottom: 40px;
        color: #fff;
    }
    &-intro {
        color: #A8A8A8;
        font: 21px/26px $semibold;
        margin-bottom: 65px;
    }
    &-content {
        margin-right: 105px;
        max-width: 620px;
        width: 100%;
    }
    &-media {
        max-width: 693px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &-list {
        li {
            position: relative;
            color: #A8A8A8;
            font: 21px/26px $semibold;
            margin-bottom: 30px;
            &:last-of-type {
                margin-bottom: 0;
            }
            &::before {
                content: '';
                width: 15px;
                height: 15px;
                background: linear-gradient(112.29deg, #00E0FF 19.07%, #00A3FF 81.84%);
                border-radius: 555px;
                display: inline-block;
                margin-right: 12px;
            }
        }
    }
}
.preview {
    overflow: hidden;
    filter: drop-shadow(0px 5px 15px rgba(0, 0, 0, 0.17));
    border-radius: 15px;
    display: flex;
    align-items: center;
    height: 254px;
    margin-bottom: 20px;
    &:before {
        content: '';
        background: rgba(0, 119, 237, 0.77);
        mix-blend-mode: color-dodge;
        box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.17);
        border-radius: 15px;
        width: 100%;
        height: 100%;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
    }
    &:after {
        content: '';
        width: 72px;
        height: 75px;
        background: url(../img/triangle.svg) no-repeat center;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        position: absolute;
    }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.video-caption {
    text-align: center;
    margin-bottom: 20px;
    font: 21px/36px $regular;
    font-style: italic;
    color: #A8A8A8;
}
.request-video {
    display: flex;
    align-items: center;
    color: #0066CC;
    font: 21px $semibold;
    svg {
        margin-left: 13px;
    }
}
/* ========================================== About ========================================== */

/* ========================================== Clients ========================================== */
.clients {
    margin-bottom: 130px;
    &-title {
        font: 48px $bold;
        margin-bottom: 50px;
        color: #fff;
        text-align: center;
    }
    &-slider {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 1020px;
        width: 100%;
        overflow: hidden;
        margin: 0 auto;
        .swiper-slide {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    &-item {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
.swiper-wrapper {
    align-items: center;
}
.control-btn {
    cursor: pointer;
    outline: none;
    background: none;
    border: none;
}
/* ========================================== Clients ========================================== */

/* ========================================== Bio ========================================== */
.bio {
    margin-bottom: 100px;
    &-title {
        text-align: center;
        font: 48px $bold;
        margin-bottom: 130px;
        color: #fff;
        text-align: center;
    }
    &-logo {
        margin-bottom: 55px;
    }
    &-name {
        margin-bottom: 35px;
        color: #fff;
        font: 21px $bold;
        text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
    }
    &-desc {
        color: #A8A8A8;
        text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
        font: 21px/36px $bold;
        margin-bottom: 30px;
    }
    &-intro {
        font: 21px/36px $regular;
        font-style: italic;
        color: #A8A8A8;
        margin-bottom: 23px;
    }
    &-link {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &-row {
        display: flex;
    }
    &-info {
        max-width: 627px;
        width: 100%;
        margin-right: 45px;
    }
    &-media {
        max-width: 720px;
        width: 100%;
    }
    &-stats {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 65px;
        &__item {
            text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
            font: 21px/26px $regular;
            color: #A8A8A8;
            span {
                color: $blue;
            }
        }
    }
    &-graph {
        margin-bottom: 45px;
        position: relative;
    }
    &-btns {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &-btn {
        margin-right: 35px;
        background: $blue;
        height: 64px;
        max-width: 160px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 90px;
        font: 21px $bold;
        color: #fff;
        transition: $transition;
        &:hover {
            background: #0066CC;
        }
    }
    &-more {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        font: 17px $bold;
        color: rgba(255,255,255,.85);
        height: 54px;
        max-width: 343px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 90px;
        background: rgba(0, 119, 237,.25);
        bottom: 70px;
    }
}
/* ========================================== Bio ========================================== */

/* ========================================== Partners ========================================== */
.partners {
    background: #F2F2F2;
    padding: 60px 0 14px;
    &-title {
        text-align: center;
        margin-bottom: 65px;
        color: #1D1D1F;
        font: 48px/58px $bold;
    }
    &-row {
        max-width: 1300px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &-item {
        max-width: 360px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 122px;
        background: #FFFFFF;
        box-shadow: 10px 15px 25px 3px rgba(0, 0, 0, 0.05);
    }
    &-wrapper {
        display: flex;
        flex-basis: 33.3%;
        justify-content: center;
        margin-bottom: 56px;
    }
}
/* ========================================== Partners ========================================== */

/* ========================================== Feedback ========================================== */
.feedback {
    padding-top: 100px;
    margin-bottom: 120px;
    &-item {
        display: flex;
        align-items: center;
    }
    &-profile {
        position: relative;
        margin-right: 100px;
        max-width: 495px;
        width: 100%;
    }
    &-name {
        text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
        font: 30px $bold;
        color: #fff;
        position: absolute;
        left: 20px;
        bottom: 20px;
    }
    &-company {
        left: 20px;
        bottom: 65px;
        position: absolute;
    }
    &-info {
        width: 100%;
    }
    &-btns {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
    }
    &-video {
        margin-right: 35px;
        background: $blue;
        height: 64px;
        max-width: 210px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 90px;
        font: 21px $bold;
        color: #fff;
        transition: $transition;
        &:hover {
            background: #0066CC;
        }
    }
    &-text {
        margin-bottom: 95px;
        position: relative;
        color: #A8A8A8;
        font: 21px/26px $bold;
        text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
        text-align: center;
        max-width: 800px;
        width: 100%;
        &::before {
            content: '“';
            text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
            font: 125px $semibold;
            color: #fff;
            position: absolute;
            line-height: 70px;
            display: flex;
            align-items: center;
            left: -20px;
            top: -40px;
        }
        &::after {
            content: '“';
            text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
            font: 125px $semibold;
            color: #fff;
            transform: rotate(180deg);
            position: absolute;
            line-height: 70px;
            display: flex;
            align-items: center;
            right: 0px;
            bottom: -30px;
        }
    }
}
/* ========================================== Feedback ========================================== */

/* ========================================== Collection ========================================== */
.collection {
    margin-bottom: 125px;
    &-title {
        text-align: center;
        color: #fff;
        margin-bottom: 120px;
        font: 48px/58px $bold;
    }
    &-row {
        display: flex;
        justify-content: space-between;
        .collection-item {
            margin-right: 90px;
            &:last-of-type {
                margin-right: 0;
            }
        }
    }
    &-image {
        margin-bottom: 35px;
        img {
            box-shadow: 10px 15px 25px 3px rgba(0, 0, 0, 0.05);
        }
    }
    &-desc {
        color: #A8A8A8;
        max-width: 370px;
        width: 100%;
        font: 21px/36px $bold;
        margin-bottom: 45px;
    }
}
/* ========================================== Collection ========================================== */

/* ========================================== FAQ ========================================== */
.faq {
    margin-bottom: 130px;
    &-title {
        text-align: center;
        margin-bottom: 95px;
        color: #fff;
        text-transform: uppercase;
        font: 48px $bold;
    }
    &-item {
        padding-bottom: 45px;
        border-bottom: 1px solid #0066CC;
        max-width: 1000px;
        margin: 0 auto 45px;
        &:last-of-type {
            margin-bottom: 0;
        }
        &:first-of-type {
            padding-top: 45px;
            border-top: 1px solid #0066CC;
        }
    }
    &-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: 20px;
        padding: 0;
        transition: $transition;
        &.active {
            padding-bottom: 45px;
            .faq-toggle {
                transform: rotate(90deg);
            }
        }
    }
    &-name {
        font: 25px $semibold;
        color: #fff;
    }
    &-toggle {
        border: none;
        background: none;
        outline: none;
        cursor: pointer;
        transition: $transition;
    }
    &-content {
        color: #A8A8A8;
        text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
        font: 21px/26px $bold;
        padding-right: 200px;
        display: none;
    }
}
/* ========================================== FAQ ========================================== */

/* ========================================== SignUp ========================================== */
.signup {
    padding-bottom: 60px;
    &-title {
        text-align: center;
        color: #fff;
        font: 48px/58px $bold;
        margin-bottom: 20px;
    }
    &-intro {
        text-align: center;
        margin-bottom: 80px;
        color: #A8A8A8;
        font: 21px/26px $bold;
    }
    &-form {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        max-width: 650px;
        width: 100%;
    }
    &-field {
        width: 100%;
        margin-bottom: 35px;
        position: relative;
        &.valid {
            .signup-valid {
                background: url(../img/correct.svg) no-repeat center;
            }
        }
        &.invalid {
            .signup-valid {
                background: url(../img/invalid.svg) no-repeat center;
            }
        }
        .signup-valid {
            position: absolute;
            right: 0;
            top: 0;
            width: 26px;
            height: 22px;
        }
        &.hint {
            .hint-el {
                opacity: 1;
                visibility: visible;
            }
        }
        input {
            width: 100%;
            border: none;
            background: transparent;
            border-bottom: 1px solid #0066CC;
            padding-bottom: 30px;
            transition: $transition;
            color: #FFFFFF;
            text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27);
            font: 21px/26px $regular;
            padding-right: 30px;
            &::placeholder {
                font: 21px/26px $regular;
                color: rgba(168, 168, 168, 0.45);
                text-shadow: 0px 4px 15px rgba(0, 0, 0, 0.27)
            }
            &:active,&:focus {
                border-color: #0080FF;
                border-width: 2px;
            }
        }
    }
    &-submit {
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;
        outline: none;
        cursor: pointer;
        border-radius: 90px;
        font: 21px $bold;
        color: #fff;
        transition: $transition;
        height: 64px;
        max-width: 120px;
        width: 100%;
        background: $blue;
        &:hover {
            background: #0066CC;
        }
    }
    .hint-el {
        position: absolute;
        right: -60px;
        top: -5px;
        transition: $transition;
        opacity: 0;
        visibility: hidden;
    }
    .hint-text {
        position: absolute;
        top: -60px;
        left: -55px;
        background: #171717;
        border-radius: 2px;
        width: 246px;
        height: 51px;
        font: 17px $regular;
        color: #fff;
        display: inline-flex;
        align-items: center;
        justify-content: center;
    }
    .hint-btn {
        border: none;
        outline: none;
        cursor: pointer;
        background: transparent;
    }
}
/* ========================================== SignUp ========================================== */

/* ========================================== Footer ========================================== */
.footer {
    background: #F2F2F2;
    padding: 66px 0 40px;
    &-row {
        display: flex;
        justify-content: space-between;
    }
    &-logo {
        margin-bottom: 35px;
        color: rgba(29, 29, 31, 0.55);
        font: 52px $regular;
        display: inline-block;
    }
    &-column {
        flex: 1;
        &--second {
            width: 100%;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            display: flex;
        }
        &__name {
            margin-bottom: 45px;
            color: #000000;
            font: 17px $semibold;
            text-align: right;
        }
    }
    &-menu {
        max-width: 270px;
        column-count: 2;
        width: 100%;
        &__item {
            margin-bottom: 40px;
            page-break-inside: avoid;
            break-inside: avoid;
        }
        &__link {
            font: 17px/21px $regular;
            color: #1D1D1F;
            transition: $transition;
            &:hover {
                color: #0066CC;
            }
        }
    }
    &-contacts {
        column-count: 2;
        text-align: right;
        &__item {
            page-break-inside: avoid;
            break-inside: avoid;
            &:last-of-type {
                margin-right: 0;
            }
        }
    }
    &-list {
        &__item {
            margin-bottom: 40px;
            &:last-of-type {
                margin-bottom: 0;
            }
        }
        &__link {
            font: 17px/21px $regular;
            color: #1D1D1F;
            transition: $transition;
            &:hover {
                color: #0066CC;
            }
        }
    }
    .contact-btn {
        margin-bottom: 60px;
    }
}
.copyright {
    color: #1D1D1F;
    font: 17px $regular;
}
.partners-slider , .burger , .phone , .collection-slider {
    display: none;
}
/* ========================================== Footer ========================================== */

@media screen and (max-width: 1200px) {
    .container {
        max-width: 992px;
    }
    .about-content {
        margin-right: 30px;
    }
    .about-list li {
        display: flex;
