    .burger {
        width: 35px;
        height: 13px;
        flex-direction: column;
        justify-content: space-between;
        border: none;
        outline: none;
        background: transparent;
        cursor: pointer;
        position: relative;
        span {
            display: block;
            width: 35px;
            height: 1px;
            background: #fff;
            transition: $transition;
        }
        &.active {
            span {
                position: absolute;
                top: 5px;
